// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'


import parallaxjs from './vue-parallax-js/src/vue-parallax-js'

Vue.use(parallaxjs, {
	// minWidth: 600
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App }
})



